import { SClientesService } from './../../services/s-clientes.service';
import { SMunicipiosService } from './../../services/s-municipios.service';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-cliente-form',
  templateUrl: './cliente-form.component.html',
  styleUrls: ['./cliente-form.component.scss']
})
export class ClienteFormComponent implements OnInit {

  public estados:any;
  public municipios:any;
  public form: FormGroup;

  constructor(private sMunicipios: SMunicipiosService,
              private formB: FormBuilder,
              private sClientes: SClientesService) {
    this.getEstados();
    this.buildForm();
   }

  ngOnInit() {
  }

  public reset(){
    this.form.reset();
  }

  private buildForm(){
    this.form = this.formB.group({
      curp: ['', [Validators.required]],
      rfc: ['', [Validators.required]],
      nss: ['', [Validators.required]],
      nombre: ['', [Validators.required]],
      apaterno: ['', [Validators.required]],
      amaterno: ['', []],
      calle: ['', [Validators.required]],
      n_ext: ['', [Validators.required]],
      n_int: [''],
      colonia: ['', [Validators.required]],
      estado: ['', [Validators.required]],
      municipio: ['', [Validators.required]],
      cp: ['', [Validators.required]],
      cr: ['', [Validators.required]],
      comentarios: ['', [Validators.required]]
    })
  }
  public getEstados(){
    this.sMunicipios.getEstados()
      .subscribe(
        (res: any) => {
          this.estados = res.response.estado;
          
        },
        err => {
          console.error(err);
          
        }
      )
  }

  public getMunicipios(){
    const edo = this.form.get('estado').value
    this.sMunicipios.getMunicipios(edo)
      .subscribe(
        (res: any) => {
          this.municipios = res.response.municipios;
        },
        err => {
          console.error(err);
          
        }
      )
  }

  public saveForm(event: Event) {
    event.preventDefault();
    if (this.form.valid) {
      console.warn('el formulario es valido');
      this.sClientes.saveCliente(this.form.value)
        .subscribe(
          (res: any) => {
            console.log(res);
            
          },
          err => console.error(err)
          
        )
      
    }else{
      console.error('el formulario no es valido');
      
    }
    console.log(this.form.value);
    
  }

}
